*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


:root {
  --white: white;
  --purple: #990099;
  --yellow: #ffd725;
  --black: black;
  --dark: #181818;
}
html, body {
  height: 100%;
}

body {
  padding-top: 75px;
  background: cornsilk;
}

a {text-decoration: none; color: black;}
ul {list-style-type: none;}



.flex-row {
  display: flex;
  flex-direction: row;
}
.flex-column {
  display: grid;
  justify-items: center;
  gap: 5px;
}

.btn {
  padding: 10px;
  background: white;
  border: 1px solid black;
  border-radius: 5px;
  cursor: pointer;
}

/* HOME */
.title {
  margin-top: 2rem;
  text-align: center;
}

.title h3 {
  font-weight: normal;
  margin-top: 0.3rem;
}

/* COMPONENTS */
.components {
  padding: 3rem 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
}
.component {
  border: 3px solid black;
  border-radius: 10px;
  overflow: hidden;
  background: var(--dark);
  color: var(--white)
}
.component img {
  width: 100%;
}
.component-info {
  padding: 1rem 1rem 1.5rem;
  font-weight: bold;
}
.component-info h2{
  margin-bottom: 0.5rem;
}

/* LOADING */
/* Ovo sredi malo posle ako te ne mrzi xD */
.loading-wrapper {
  z-index: 100;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: gray;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
.loading-wrapper h1 {
  font-size: 5rem;
}